<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="班组名称" prop="name" ref="name">
                <a-input
                  v-model="entity.name"
                  placeholder="请输入班组名称"
                  name="name"
                  @change="inChange"
                ></a-input>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="组长" prop="leader">
                <a-input v-model="entity.leader" placeholder="请输入组长姓名" name="leader" />
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="成员" prop="members">
                <a-select
                  mode="multiple"
                  v-model="entity.members"
                  placeholder="请选择班组成员"
                  name="members"
                >
                  <a-select-option value="张三">张三</a-select-option>
                  <a-select-option value="李四">李四</a-select-option>
                  <a-select-option value="王五">王五</a-select-option>
                  <a-select-option value="赵六">赵六</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="请输入描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <g-collapse headerName="其他信息">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <!-- 创建人 -->
                <a-form-model-item label="创建人">
                  <span>admin</span>
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <!-- 最近修改时间 -->
                <a-form-model-item label="修改时间">
                  <span> 2025-04-20 15:00</span>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  
  export default {
    name: "CreateOrEditTeam",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          name: "",
          leader: "",
          members: [],
          description: "",
        },
        rules: {
          name: [{ required: true, message: "班组名称不能为空", trigger: "change" }],
          leader: [{ required: true, message: "组长不能为空", trigger: "change" }],
          members: [
            { required: true, message: "请选择班组成员", trigger: "change" },
          ],
        },
      };
    },
  
    mounted() {},
  
    methods: {
      // 初始化
      init() {
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
            this.entity = {
              name: "生产一组",
              leader: "张三",
              members: ["李四", "王五"],
              description: "负责生产线A的生产任务",
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        // 重置表单绑定的数据源
        this.entity = {
          name: "",
          leader: "",
          members: [],
          description: "",
        };
      },
      async save() {
        // ... existing code ... (和 create-or-edit-shift.vue 中的 save 相同)
         let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.name);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          });
        }
      },
  
      inChange(e) {
        console.log(e.target.value);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>